import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, Perfil } from '../../../../models/dtos/ger/perfil';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'descricao',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const PerfilSearchView: FC<SearchViewProps<Perfil>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
  ];

  const getSearchExpression = (): SearchExpression[] => {
    const expressions: SearchExpression[] = [];
    const { usuario, perfil } = globalReducer.globalParameter;

    if (usuario.id !== 1) {
      if (perfil.id === 1) {
        expressions.push({
          fieldName: 'id',
          value: 'id',
        });
      }
    }

    return expressions;
  };

  return (
    <WindowComponent
      title="Configuração dos Perfis de Usuários"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      wikiId={1558}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'PerfilEditView', CLASS_NAME)}
        searchExpressions={getSearchExpression}
      />
    </WindowComponent>
  );
};

export default PerfilSearchView;
